import axios from 'axios'

const getMovie = (id) => {
    const request = axios.get(`http://localhost:3001/api/movie/${id}`)
    return request.then(response => response.data)
  }

const getMovieCast = (id) => {
    
    const request = axios.get(`http://localhost:3001/api/movie/${id}/credits`)
    return request.then(response => response.data)
  }


export default {getMovie, getMovieCast}